import React from 'react'
import './contact.css'
import LocationOnOutlinedIcon from '@mui/icons-material/LocationOnOutlined';

export default function ContactForm() {
    return (
        <div>
            <div className="HeadText">
                    <h1>Contact</h1>
                    <div className="UnderLine"></div>
                    <p className='abText1'>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Saepe nihil itaque quidem? Odio, beatae. nam amet laboriosam!</p>
                </div>
            <section id="contact">

                

                <div class="contact-wrapper">
                    {/* <!-- Left contact page --> */}
                   

                    <div class="direct-contact-container">

                        <div className="left">
                            <div className="Card1">
                                <LocationOnOutlinedIcon id='IconGG' />
                                <h4 id='HeadGG'>Location</h4>
                                <p id='subGG'>A10 Addam Streat,NewYork</p>
                            </div>
                            <div className="Card1">
                                <LocationOnOutlinedIcon id='IconGG' />
                                <h4 id='HeadGG'>Email</h4>
                                <p id='subGG'>Addam@gmail.com</p>
                            </div>
                            <div className="Card1">
                                <LocationOnOutlinedIcon id='IconGG' />
                                <h4 id='HeadGG'>Call</h4>
                                <p id='subGG'>+91 987456321</p>
                            </div>
                            <div className="Card1">
                                <LocationOnOutlinedIcon id='IconGG' />
                                <h4 id='HeadGG'>Open Hours</h4>
                                <p id='subGG'>Mon-Fri 10AM-5PM</p>
                            </div>
                        </div>
                        <hr />


                    </div>



                    {/* <!-- Left contact page --> */}

                    <form id="contact-form" class="form-horizontal" role="form">

                        <div class="form-group">
                            <div class="col-sm-12">
                                <input type="text" class="form-control" id="name" placeholder="NAME" name="name" value="" required />
                            </div>
                        </div>

                        <div class="form-group">
                            <div class="col-sm-12">
                                <input type="email" class="form-control" id="email" placeholder="EMAIL" name="email" value="" required />
                            </div>
                        </div>

                        <textarea class="form-control" rows="10" placeholder="MESSAGE" name="message" required></textarea>

                        <button class="btn btn-primary send-button" id="submit">
                            SUBMIT
                        </button>

                    </form>

               
                </div>

            </section>

        </div>
    )
}
