import React ,{useEffect} from 'react'
import './frontadd.css'
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import PersonalVideoIcon from '@mui/icons-material/PersonalVideo';
import LocationOnOutlinedIcon from '@mui/icons-material/LocationOnOutlined';
import GpsNotFixedOutlinedIcon from '@mui/icons-material/GpsNotFixedOutlined';
import AcUnitIcon from '@mui/icons-material/AcUnit';
import AOS from 'aos';
import 'aos/dist/aos.css';

export default function Frontadd() {
  useEffect(() => {
    AOS.init();
  }, [])
  return (
    <>
      <div className="FrontContainer">
        <div className="textBox">


          <div data-aos="fade-up"
            data-aos-duration="3000">
        


          <h1 id='MainHead'>Welcome to Impact</h1>
          <p id='SubHead'>Lorem ipsum dolor sit amet consectetur adipisicing elit. Tempora sed aut inventore quod itaque est enim ipsam in nemo vel adipisci corporis, minima expedita, quisquam, totam iusto natus? Unde, quae.</p>
          <button className='GetStartedBtn'>Get Started</button>
          <div className="watchalign">
            <PlayCircleOutlineIcon id='MainPlaybtn' />
            <p id='WatchText'>Watch Video</p>
          </div>
        </div>
        </div>
        <div className="ImageBox">
          <div className="Imgfit">
            <img src='undraw_engineering_team_a7n2.svg' id='ImgResize' />
          </div>
        </div>
      </div>
      <div className="cardArea">
        
        <div className="card1">
          <PersonalVideoIcon id='iconAlign' /><p id='IconText'>Lorem Ipsem</p></div>
        <div className="card1"><AcUnitIcon id='iconAlign' /><p id='IconText'>Set ut perspiciated</p></div>
        <div className="card1"><LocationOnOutlinedIcon id='iconAlign' /><p id='IconText'>Magni Dolores</p></div>
        <div className="card1"><GpsNotFixedOutlinedIcon id='iconAlign' /><p id='IconText'>Nemo Enim</p></div>
      </div>
    </>
  )
}
