import React from 'react'
import './topdata.css'
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import PhoneIphoneIcon from '@mui/icons-material/PhoneIphone';
import TwitterIcon from '@mui/icons-material/Twitter';
import FacebookRoundedIcon from '@mui/icons-material/FacebookRounded';
import InstagramIcon from '@mui/icons-material/Instagram';
import LinkedInIcon from '@mui/icons-material/LinkedIn';

export default function Topdata() {
    return (
        <>
            <div className='Topcontainer'>
                <nav className="navbarM">
                    <p className="navM-brand">
                        <MailOutlineIcon id='muIcon2' />Contact@example.com 
                        <PhoneIphoneIcon id='muIcon2' />+1 5589 5548855
                    </p>
                    <ul className="navM-links">
                        <TwitterIcon id='muIcon' />
                        <FacebookRoundedIcon id='muIcon' />
                        <InstagramIcon id='muIcon' />
                        <LinkedInIcon id='muIcon' />
                    </ul>

                </nav>
            </div>
        </>

    )
}
